	export default {
		data() {
			return {
				// basic
				basicTitle: '',
				basicPhone: '',
				basicCity: ['重庆市', '重庆市', '渝中区'],
				teachTime: '', // 授课时间
				modalName: '',
				checkbox: [{
					value: 0,
					name: '周日',
					checked: false,
